.modalWindow {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0 ;
    left: 0;
    display: none;
    background: rgba(0,0,0,0.5);
}

.modalWindowContent {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 25px;
    background: aliceblue;
    border-radius: 16px;
    min-width: 250px;
}


.modalWindow.active {
    display: flex;
    justify-content: center;
    align-items: center;

}